import Table from '../../mo/models';

export default function getVisibleCellOfCol(table: Table, colIndex: number) {
  for (let i = 0; i < table.nodes.length; i += 1) {
    const row = table.nodes[i];
    const cell = row.nodes[colIndex];
    if (!cell) {
      return null;
    }
    if (cell && !cell.data.hidden) {
      return cell;
    }
  }
  return null;
}
